
.mainContent{
    overflow:hidden;
    overflow-y: hidden;
    display: block;
    width:736px;
    background-color:  #F5F8FA;
    height: 760px;
    transition: all ease 0.2s;

}


.blurScreen{
    position: absolute;
    left: 0;
    top: -20px;
    z-index: 2;
    width: 100vw;
    height: 100vh;
    -webkit-backdrop-filter: blur(10px); /* Safari 9+ */
    backdrop-filter: blur(10px); /* Chrome and Opera */
    background-color: rgba(255, 255, 255, 0.3);

}


.feed{

    position: absolute;
    left:0;
    top: -15px;
    z-index: 100;
    width: 100vw;
    transition: ease-in-out 0.2s;
    transform: translateY(2%);
}

.feed-hidden{
    position: absolute;
    transition: ease-in-out 0.2s;
     transform: translateY(120%);
}



::-webkit-scrollbar {
    display: none;
}


.functionBar{
    position: relative;
    display: flex;
    justify-content:flex-start;
    background-color: #F5F8FA;
    width: 1000px;
}

.functionBtns{
    display: flex;
    overflow: hidden;
}

.functionBar text{
    cursor: pointer;
    margin: 5px;
    padding: 0px 10px ;
    color: #202160;
}

.functionBtns text:hover{
    border-radius: 5px;
    background-color:#F1F3F4;
}

.resumeBtn{
    margin-left: 20px;
    cursor: pointer;
    color: rgb(24, 91, 136);
}
.resumeBtn:hover{
}





.content-view{
    display:flex;


}

.candidateList{
    transition: all ease-in-out 0.5s;
    height: 590px;
    margin-top: 0px;
    overflow-y: scroll;
    padding:10px;
}

.candidate-view{
    border-radius: 0px;
    margin-bottom: 10px;
    background: #fff;
    width: 100%;

    overflow-y: hidden;
}

.candidate-view:hover{
    box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
}

.candidate-name{
    cursor: pointer;
    color: black;
    display: flex;
    align-items: center;
    margin-inline: 10px;
    font-size: 20px;

}
.candidate-name:hover{
    color: #2C3E50;
}

.score{
    display: flex;
    margin:15px;
    border-radius: 100%;
    border-width: 4px;
    border-style: solid;
    border-color: chartreuse;
    justify-content: center;
    align-items: center;
    width: 60px;
    height: 60px;
}

.scoreNumber{
    color: black;
    font-weight: 500;
    font-size: 20px;
}

.chart{
    background-color: transparent;
    transform: translateY(-34%);
    cursor:pointer
}

.skills{
    margin-left: 20px;

}

.skills .education .experience{
    padding-block: 5px;
}
.education{
    padding:20px;
    padding-block: 5px;
}
.experience{
    padding:20px;
    padding-block: 5px;
}

.skills text{

    background-color: #dbf0f071;
    border-radius: 5px;
    font-weight: 500;
    letter-spacing: 0.8px;
    margin-right:12px;
    font-size: 14px;
    color: dodgerblue;
}



.education text{
    background-color: #F2F9FD;
    border-radius: 5px;
    letter-spacing: 1px;
    font-weight: 500;
    margin-right:12px;
    font-size: 14px;
    color: #546E7A;
}
.experience text{
    background-color: #546E7A;
    border-radius: 20px;
    padding-inline: 8px;
    padding-top:3px;
    letter-spacing: 0.8px;
    padding-bottom:5px;
    font-weight: 500;
    margin-right:12px;
    font-size: 14px;
    color: white;
}
.candidate{

}

.titles{

    margin-top: 10px;
    margin-left: 5px;
    display: flex;
}

.titles text{
    color: dodgerblue;
    font-size:14px;
}

.college{
    margin-top: 5px;

    justify-content: flex-start;
    display: inline-flex;
}

.college text{
    font-size: 13px;
    padding-inline: 5px;
    margin-inline: 2px;
    font-weight: bold;
    border-radius: 25px;
    background-color: #F1F3F4;
    color: #70757A;
}


.bottom-Bar{
    transform: translateY(20%);
    margin-bottom: 10px;

}
.overlay-hidden{
    transition: ease 0.5s;

    height: 0px;
    overflow: hidden;
}

.overlay{
    transition: ease 0.5s;
    height: 300px;
    overflow: hidden;
}

.addJob{
    display: flex;
    margin-bottom: 20px;
    margin-right:10px;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    padding:5px 10px;
    color: black;
    border-color: black;
    border-style: solid;
    border-width: 2px;
    font-weight: 500;
    border-radius: 0px;
    background-color: white;
}

.addJob:hover{
    border-color: dodgerblue;
    border-width: 2px;
    background-color: #F2F9FD;

}








.showBtn{
    display: flex;
    align-items: center;
    margin:5px;
    justify-content: center;
    height: 35px;
    width: 100px;
    cursor: pointer;
    border-radius: 30px;
    position: relative;
    box-shadow: 1px 2px 6px 0px rgba(0,0,0,0.2);
    transition: all 300ms ease;
    background-color: white;
}

.showBtn text{
    padding-inline: 5px;
    color: #202160;
    font-size: 14px;
}

.showBtn-rotated text{
    padding-inline:5px;
    color: #202160;
    font-size: 14px;
}
.showBtn text:hover {
    background-color: transparent;
}
.showBtn-rotated text:hover {
    background-color: transparent;
}


.showBtn:hover {
    box-shadow: 0 6px 12px rgba(0,0,0,.275);
  }


.showBtn-rotated {
    display: flex;
    align-items: center;
    justify-content: center;
    align-self: flex-start;
    margin:5px;
    height: 35px;
    width: 100px;
    cursor: pointer;
    background-color: white;
    border-radius: 30px;
    transition: all 300ms ease;
    box-shadow: 1px 2px 6px 0px rgba(0,0,0,0.2);
}
.showBtn-rotated:hover {
    box-shadow: 0 6px 12px rgba(0,0,0,.275);
  }

.showBtn img{
      transition: ease 0.5s;
  }
.showBtn-rotated img{
    transition: ease 0.5s;
    transform: rotate(45deg);
}