



.feedBG{
    display: flex;

    justify-content: flex-start;
    align-items: center;
    color: dodgerblue;
    overflow: hidden;
    background: #080808;
 
}
.feedView{

    background-color: white;
    overflow: hidden;
}

.feedBar{
    display: flex;
    justify-content: center;
    align-items: center;
    width:  100%;
    
}

.feedBar text{
    display: flex;
    justify-content: center;
    width: 120px;
    cursor: pointer;
    color: grey;
    font-size: 18px;
}
.feedBar-btn-active text{
    padding-inline: 0px;
    transition: all ease 0s;
    font-size: 18px;
    color: dodgerblue;
    background-color: #181818;
    font-weight: 500;
    
}

.closeIcon{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 30px;
    margin-right: 50px;
    padding:4px;
    border-radius: 20px;
    background-color: #DFF8EE;
    border:3px solid transparent;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;

}
.closeIcon:hover{
    cursor: pointer;

    border-color: rgba(30, 143, 255, 0.219);


}


.detailContainer{
    height: 90vh;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    background-color:  white;
}

.detail-card{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-block: 20px;
}

.details{
    display: flex;
    width: 400px;
    padding:20px;
    justify-content: space-between;
}


.inputBar{
    border: transparent;
    color: dodgerblue;
    font-size: 17px;
    font-family: 'Montserrat';
}


.feed-gradient {
    position: relative;
    z-index: 50;
    background: repeating-linear-gradient(to right, #5affd5 0%, #56a2ff 50%, #5affd5 100%);
    height: 5px;
    background-size: 200% auto;
    background-position: 0 100%;
    animation: gradient 2s infinite;
    animation-fill-mode: forwards;
    animation-timing-function: linear;
  }
  
  @keyframes gradient { 
    0%   { background-position: 0 0; }
    100% { background-position: -200% 0; }
  }



  .feed-content{
    background-color:  #1E1E1E;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
    height: 200px;
    overflow-y:scroll ;
    padding-bottom: 0px;
}
  