section{
    z-index: s0;
    position:absolute;
    bottom: -25px;
    width: 100%;
    height: 11vh;
    background:transparent;
    overflow: hidden;
    border: bottom 2px;
}



section .wave{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 80px;
    background: url(wave.png);
    background-size: 1000px 100px;
}

section .wave.wave1
{

    animation: animate1 15s linear infinite;
    z-index: 1000;
    opacity: 1;
    animation-delay: 0s;
    bottom: 0;
}
section .wave.wave2
{
    animation: animate2 20s linear infinite;
    z-index: 999;
    opacity: 0.5;
    animation-delay: -5s;
    bottom: -10px;
}
section .wave.wave3
{
    animation: animate3 15s linear infinite;
    z-index: 998;
    opacity: 0.2;
    animation-delay: -2s;
    bottom: -15;
}
section .wave.wave4
{
    animation: animate4 20s linear infinite;
    z-index: 997;
    opacity: 0.7;
    animation-delay: -5s;
    bottom: -20px;
}

section .wave.wave1-load
{

    animation: animate1 5s linear infinite;
    z-index: 1000;
    opacity: 1;
    animation-delay: 0s;
    bottom: 0;
}

section .wave.wave2-load
{
    animation: animate2 8s linear infinite;
    z-index: 999;
    opacity: 0.5;
    animation-delay: -5s;
    bottom: -10px;
}
section .wave.wave3-load
{
    animation: animate3 5s linear infinite;
    z-index: 998;
    opacity: 0.2;
    animation-delay: -2s;
    bottom: -15;
}
section .wave.wave4-load
{
    animation: animate4 8s linear infinite;
    z-index: 997;
    opacity: 0.7;
    animation-delay: -5s;
    bottom: -20px;
}

@keyframes animate1
{
    0%{
        background-position-x: 0;
        
    }
    100%{
        background-position-x: 1000px ;
    }
}
@keyframes animate2
{
    0%{
        background-position-x: 0;
        
    }
    100%{
        background-position-x: 1000px ;
    }
}
@keyframes animate3
{
    0%{
        background-position-x: 0;
        
    }
    100%{
        background-position-x: 1000px ;
    }
}
@keyframes animate4
{
    0%{
        background-position-x: 0;
        
    }
    100%{
        background-position-x: 1000px ;
    }
}





