.container_mainview{
    margin-top: 45px;
    height: 100vh;
}




.tab-view{
    display: flex;
    align-items:flex-start;
    background-color:transparent;
}



  



.tab-container{

    cursor: pointer;
    display: flex;
    justify-content:space-evenly;
    background: #2C3E50;
    align-items: center;
    padding-inline: 10px;
    margin-right: 5px;
    text-align: center;
    height: 30px;
    color: dodgerblue;
}

.tab-container:hover{
    color: #d2ecb7;
}


.tab-container-active{
    cursor:pointer;
    display: flex;
    justify-content:space-evenly;
    background: dodgerblue;
    color: #F5F8FA;
    align-items: center;
    padding-inline: 10px;
    height: 30px;
    margin-right: 5px;
    text-align: center;
}


.icon-closetab{
    margin-top: 2px;
    position: relative;
    margin-left: 10px;
    color: dodgerblue;
    padding:1px;
}

.tab-container:hover .icon-closetab{
    color:   #99FF33;
}
.tab-container:hover .icon-closetab{
    transition: ease-in-out 0.1s;
    color: #99FF33;
    padding:0px;
    cursor: pointer;
}

.icon-closetab-active{
    margin-top: 2px;
    position: relative;
    margin-left: 10px;
    color: white;
    padding:1px;
}
.icon-closetab-active:hover{
    transition: ease-in-out 0.1s;
    padding:0px;
    cursor: pointer;
}





/* .addIcon{

    cursor:pointer;


} */
.addTabIcon .tabIcon{
    margin-right: 10px;
    transition: all ease-in 0.1s;
}


.tab-options{

    width: 150px;
    display: none;
    top: 70px;
 position: absolute;
    z-index: 20;
    background: white;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
}


.addTabIcon:hover .tabIcon{
    transition: all ease-in 0.1s;
    transform: rotate(45deg)
}

.addTabIcon:hover .tab-options{
    display: block;
}







.tab-options .option {
    color: black;
    padding: 12px 16px;
    width: 100%;
    text-decoration: none;
    font-size: 14px;
    display: block;
  } 
.tab-options .option:hover {background-color: #F1F3F4;}




body {
    background: #222;
    margin: 0;
    padding: 0;
    color: #F5F8FA;
  }
  
  .animated-gradient {
    position: relative;
    z-index: 50;
    background: repeating-linear-gradient(to right, #5affd5 0%, #56a2ff 50%, #5affd5 100%);
    height: 5px;
    background-size: 200% auto;
    background-position: 0 100%;
    animation: gradient 2s infinite;
    animation-fill-mode: forwards;
    animation-timing-function: linear;
  }
  
  @keyframes gradient { 
    0%   { background-position: 0 0; }
    100% { background-position: -200% 0; }
  }