.dateView{
    overflow-y: scroll;
}


.line{
    background-color: lightblue;
}
.line:hover{
    cursor: pointer;
    background-color: rgba(144, 238, 144, 0.438)
}

.line-active{
    transition: all ease-in 0.2s;
    background-color: dodgerblue;
}


.feed-btn{
    cursor: pointer;
    background-color: white;
    border: 2px solid transparent;
    padding-inline:20px;

}

.feed-btn text{
    color: black;
}

.feed-btn-active{

    transition: all ease-in 0.0s;
    background-color:dodgerblue;
    border: 2px solid black;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-inline:20px;
    border-radius: 20px;

}

.slider-details{
    /* box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px; */
    width: 400px;
    height: 200px;
    margin-top: 10px;
    border-radius: 0px;
    background-color: white;
}