.jobView{
    z-index: 10;
    width: 400px;
    height: 100vh;
    margin-top: 75px;
transition: all ease 1s;
background:linear-gradient(#302F3E,#2E2D3B) ;
overflow-x: hidden;
}
.jobView-exp{
    transition: all ease 1s;
    width: 1537px;
    background:linear-gradient(#302F3E,#2E2D3B) ;
    overflow-x: hidden;
    }

.job-TopView{
    display: block;
    padding-top: 19px;
    background-color: #2C3E50;

}

.expandBtn{
    position: absolute;
    top: 10;
    right: 20px;
    padding:5px;
    margin:5px;
    transition: all ease 2s;
    transform: rotate(180deg);
    border-radius: 100%;
    border: solid transparent 1px;
}
.expandBtn:hover{
    transition: all ease 0.1s;
    cursor:pointer;
    background-color: #545768;
}
.expandBtn-exp{
    transition: all ease 2s;
    position: absolute;
    top: 10;
    right: 20px;
    padding:5px;
    margin:5px;
    border-radius: 100%;
    border: solid transparent 1px;
}
.expandBtn-exp:hover{
    transition: all ease 0.1s;
    cursor:pointer;
    background-color: #545768;
}




.screenBtn{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100px;
    align-items: center;
    padding-inline: 60px;
    width: 100px;
    font-size: 22px;

}

.screenBtn:hover{
    color: dodgerblue;
}




.screenBar{
    transition: all ease-in-out 0.2s;
    display:flex;
    align-items: center;
    justify-content: flex-start;
    width: 0px;
    margin-left: 0px;
    height: 30px;
}
.screenBar-exp{
    transition: all ease-in-out 0.2s;
    margin-left: 26px;
    display:flex;
    align-items: center;
    justify-content: flex-start;
    width: 100px;
    height: 30px;
}

.screenText{
    display: flex;
    align-items: flex-start;
    justify-content: center;
    text-align: center;
    height: 100px;
    padding-bottom: 20px;
    width: 100px;
    padding-inline: 60px;
    letter-spacing: 1px;
    align-items: center;
    

}

.jobTitle{
    color: dodgerblue;
    padding-left: 160px;
}


.createBtn{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 80px;
    padding:2px 10px;
    margin-left: 5px;
    background-color: #545768;
    border: solid transparent 1px;

}

.createBtn:hover{
    transition: all ease 0.1s;
    cursor:pointer;
    border: solid white 1px;
}

.createBtn text{
    padding-inline: 5px;
}


.searchBtn{
    display: flex;
    align-items: center;
    justify-content: flex-start;
    width: 210px;
    padding:2px 10px;
    background-color: #545768;
    border: solid transparent 1px;
}


.searchBtn:hover{
    transition: all ease 0.1s;
    cursor:pointer;
    border: solid white 1px;
}
.searchBtn text{
    padding-inline: 5px;
}

.addJobBtn{
    color:   white
}

.jobList{
    transition: all ease 1s;
    display: block;
    height: 615px;
    overflow-y: scroll;
    overflow-x: hidden;
    padding:5px;
    background:linear-gradient(#302F3E,#2E2D3B) ;
}

.jobList-exp{
    transition: all ease 1s;
    display: block;
    height: 700px;
    overflow-y: scroll;
    padding:5px;
    background:linear-gradient(#302F3E,#2E2D3B) ;
}



.job-container{
    justify-content: center;
    align-items:center;
    margin-bottom: 5px;
    background:#414350;
    background-size: 400% 400%;
    -webkit-animation: Gradient 20s ease infinite;
    -moz-animation: Gradient 20s ease infinite;
    animation: Gradient 20s ease infinite;
    box-shadow: rgba(17, 12, 46, 0.15) 0px 48px 100px 0px;
    /* box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px; */
}


.job-container:hover{
    cursor: pointer;
    background:#414350be;
}

.job-title{
    color: #FEFEFE;
    font-size: 18px;
    font-weight: 500;
}

.job-loc{
    letter-spacing: 0.5px;
    color: #a7aac0;
}

.job-status{
    color: #a7aac0;
    border-bottom: 2px #494B58 solid;
}

.job-hired{

    color: #a7aac0;
}


.Added-total{
    transition: all ease 0.1s;
    border: 2px solid transparent
    ;
}
.Added-unique{
    transition: all ease 0.1s;
    border: 2px solid transparent;
    
}
.Added-unparsed{
    transition: all ease 0.1s;
    border:2px solid transparent;
    
}
.Added-total:hover{
    transition: all ease 0.1s;
    border:2px solid #494B58;

}
.Added-unique:hover{
    transition: all ease 0.1s;
    border: 2px solid #494B58;
}
.Added-unparsed:hover{
    transition: all ease 0.1s;
    border: 2px solid #494B58;
    
}

