.mainview{
    transition: all ease 0s;
    background-color: #080808;
    height: 650px;
    width: 400px;
    margin-top: 75px;
    display: flex;
    flex:1;
    flex-direction: column;
    justify-content: space-evenly;
    
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2); 
}


.expandBtn-1{
    position: relative;
    padding:5px;
    transition: all ease 0.2s;

    border-radius: 100%;
    border: solid transparent 1px;
}
.expandBtn-1:hover{

    cursor:pointer;
    background-color: #545768;
}
.expandBtn-1-exp{
    transition: all ease 0.2s;
    position: relative;

    transform: rotate(180deg);
    padding:5px;

    border-radius: 100%;
    border: solid transparent 1px;
}
.expandBtn-1-exp:hover{

    cursor:pointer;
    background-color: #545768;
}


.sideview{
    display: flex;
    flex:1;
    align-items: center;
    justify-content: flex-start;
    flex-direction: column;
    
}
.sideview-title{
    display: 'flex';
    text-align: center;
    padding-inline: 10px;
    margin-left: 0px;
    font-size: 22px;
    font-weight: 500;
    margin-block: 10px;
    padding-top: 10px;
    width: 100%;
    color: #FEFEF7; 

    background-color:transparent
}




.card-view{
    width: 90%;
    height: 100px;
    padding-block: 10px;
    
    border-radius: 8px;
    margin-bottom: 10px;
    background-color: #181818;
    box-shadow: 1px 2px 6px 0px rgba(0,0,0,0.1);

}
.card-view-1{
    height: 80px;
    border-radius: 8px;
    width: 300px;
    margin-bottom: 20px;
    box-shadow: 1px 2px 6px 0px rgba(0,0,0,0.1);
}


.info-1{
    font-size: 22px;
    color: dodgerblue;
    text-align: center;
}

.info-2{
    font-size: 15px;
    color: white
}


.tile-btn{
    border: 1px solid;
    border-color: transparent;
}

.tile-btn:hover{
    border-radius: 8px;
    cursor: pointer;
    background-color: #252525;

}

.resolve-btn:hover{
    font-size: 18px;
    background: #252525;
    border:1px solid;
    border-color: "#7FA81F";
    cursor: pointer;
 
}



.resolveBtn:hover{
    background: #FBE9E7;
}

.stats-btn:hover{
    border-radius: 0px;
    cursor: pointer;
    background: #d3f5f596;
}




.icon-sidebar:hover h5{
    transition: 0.05s ease-in;
    color: #1F65DB;
}
.icon-sidebar:hover{
    transition: 0.05s ease-in;
    color: #1F65DB;
}



.dropdown-content {
    position: absolute;
    right: 0;
    display: none;
    background-color:  white;
    min-width: 180px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
    font-size: 14px;
  }
.dropdown-content text {
    cursor: pointer;
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
  }
.icon-sidebar:hover .dropdown-content{
    display: block;
    transition: 10s ease-in;

}
.dropdown-content text:hover {background-color: #F1F3F4;}

h5{
    color: dodgerblue;

}

.icon-sidebar{
    height: 30px;
    width: 30px;
}