.settings-main{
    overflow:hidden;
    overflow-y: hidden;
    width:736px;
    display: block;
    background-color:  #080808;
    height: 760px;
    padding-bottom:20px
}

.settings-menu{
    background-color: #2525258c;
    display: flex;
    flex-direction: column;
    margin-top: 35px;

}

.settings-menu-btn{
    padding:13px;
    width: 180px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    cursor: pointer;
    /* background-color: #252525; */

    margin-inline: 5px;
    margin-block: 5px;
    color: #FEFEFE;
    border-radius: 15px;

}

.settings-menu-btn:hover{
    background-color: #252525;
}


.settings-menu-btn text{
    margin-left: 10px;
    width: 100px;


}

.menu-view{

    width: 100%;
}


.settings-tab-bar{
    display: flex;
    margin-left: 40px;
    justify-content: flex-start;
    align-items: center;
    background-color: transparent;

}

.settings-tab-bar-btn-active{
    cursor:default;
    transition: all ease-in-out 0.1s;
    align-items: center;
    margin-inline-end: 20px;
    padding-block: 5px;
    color: #FEFEFE;
    border:1px solid #A9ABB3;
    padding-inline: 10px;
    border-top: transparent;
    border-left: transparent;
    border-right: transparent;
    text-align: center;

}
.settings-tab-bar-btn{
    transition: all ease-in-out 0.1s;
    cursor: pointer;
    margin-inline-end: 20px;
    padding-block: 5px;
    padding-inline: 10px;
    color: #fefefebb;
    text-align: center;
    background-color: transparent;
}


.menu-content{
    background-color:  #1E1E1E;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    border: 0px solid transparent;
    border-top-color: #A9ABB3;
    width: 100%;
    height: 580px;
    overflow-y:scroll ;
    padding-bottom: 60px;
}

.profile{
    color: #dddddd4d;
    align-items: center;
    display: flex;
    margin-block: 40px;
    color: rgba(30, 143, 255, 0.678);
    margin-left: 100px;
   flex-direction: column;
    position: relative;
    background-color: transparent;
    

}
.profile:hover{
    
}
.profile text{
    cursor: pointer;
    color: #DDDDDD;
    padding:10px 10px;
    font-size: 17px;
}

.menu-card{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 20px;
    width: 100%;
    background-color:transparent ;
}

.menu-card text{
    width:120px;
    color: #DDDDDD;
}

.menu-card input{
    text-align: start;
    padding: 3px 15px;
    width: 300px;
    color: dodgerblue;
    background-color:  transparent; 
    border: 0px;
    
}
.menu-card input:focus{
    background-color:  #3C3C3C; 
    color: white;
}

