
body{
  overflow-x: hidden;
  overflow-y: hidden;
}



.side-hidden{
  transform: translateX(100%);
  transition: all ease 0.5s;
}


.side{
  display: flex;
  width: 400px;
  transform: translateX(0%);
  transition: all ease 0.5s;
}



.main{
  display: flex;
    transition: all ease 0.5s;
   width: 100%;
}
.main-hidden{

  transition: all ease 0.5s;
}
.jobs{
  display: flex;
  width: 400px;
  transform: translateX(0%);
  transition: all ease 0.5s;
}

.jobs-hidden{

  transform: translateX(-100%);
  transition: all ease 0.5s;
}



.skillate{
  display: flex;
    position: absolute;
    top: 16px;
    left: 10px;
    z-index: 1;

}

.skillate h1{
    font-size: 35px;
    margin-left: 10px;
    color:white;
    
}

.skillate .img{
  transition: all ease 0.5s;
    height: 40px;
    cursor: pointer;
    width: 40px;
    margin-top: 3px;
    border-radius: 100%;
    border-color: transparent;
    border-style: solid;
    border-width: 1px;
    box-shadow: 1px 2px 6px 0px rgba(0,0,0,0.2);


}

.skillate .img:hover{
    border-color: dodgerblue;
    transition: all ease 0.5s;
    border-style: solid;
    border-width: 1px;
    box-shadow: 0 6px 12px rgba(0,0,0,.275);
}


.skillate .img-active{
    transition: all ease 0.5s;
    height: 40px;
    cursor: pointer;
    width: 40px;
    margin-top: 3px;
    transform: rotate(90deg);
    border-radius: 100%;
    border-color: transparent;
    border-style: solid;
    border-width: 1px;
    box-shadow: 1px 2px 6px 0px rgba(0,0,0,0.2);    
}


.skillate .img-active:hover{
    border-color: dodgerblue;
    border-style: solid;
    border-width: 1px;
    box-shadow: 0 6px 12px rgba(0,0,0,.275);
}


.mainscreen{
        
        display: flex;
        flex-direction: column;
        height: 100vh;
        width: 100vw;
        justify-content: flex-start;
        align-items: flex-start;
        color: #fff;
        overflow: hidden;
        z-index: -5;
        background: linear-gradient(-45deg, #2C3E50, #4CA1AF);
        background-size: 400% 400%;
        -webkit-animation: Gradient 8s ease infinite;
        -moz-animation: Gradient 8s ease infinite;
        animation: Gradient 8s ease infinite;
    
}


